.wrapper {
    position: relative;
    height: 75px;
    margin: 40px;
    /* padding-top: 50px; */
    padding-right: 100px;
}

.line {
    position: absolute;
    left: 100%;
    top: 0;
    bottom: 0;
    height: 80px;
    width: 2px;
    background: #ccc;
    z-index: 1;
}

.wordwrapper {
    text-align: center;
    height: 12px;
    position: absolute;
    left: -9px;
    right: 0;
    top: 50%;
    margin-top: -12px;
    z-index: 2;
    
}

.word {
    color: #ccc;
    text-transform: uppercase;
    letter-spacing: 4px;
    padding: 3px;
    background: #fff;
    -webkit-writing-mode: vertical-rl;
            writing-mode: vertical-rl;
    text-orientation: sideways;
    margin-top: 25px;
}

.social-media {
    position: relative;
    left: 90%;
    z-index: inherit;
}

span {
    font-family: 'Bowlby One SC', cursive;
	font-size: 9rem;
	text-align: center;
	line-height: 150%;
  color: rgb(227, 119, 4);
  letter-spacing: 4px;
}

p{
  font-family: 'Montserrat', sans-serif;
}

section {
