/* Pattern styles */
.column-one {
  background-color: #fff6ec;
  float: left;
  width: 50%;
  height: 100vh;
}
.column-two {
  background-color: #feefdf;
  float: left;
  width: 50%;
  height: 100vh;
}
@media screen and (max-width: 500px) {
	.column {
	  width: 100%;
	}
  }

.container {
	border-bottom: 1px solid #f1f1f1;
    padding: 5.5em 0;
    background: #f8f8f8;
	text-align: center;
	position: relative;
	height: 460px;
}

.page-wrap {
	margin: 0 auto;
    width: 75%;
    max-width: 1600px;
}

img {
	float: left;
	width: 25%;
	/* border-radius: 50%; */
	
}

#header {
	font-size: 4rem;
	text-transform: uppercase;
	width: 100%;
}

#description {
	font-size: 1rem;
	font-family: 'Montserrat', sans-serif;
	line-height: 2rem;
	justify-content: start
}
.box {
	margin: 0 auto;
    width: 75%;
	max-width: 1600px;
	text-align: center;
	height: 100%;
}

#title {
	font-size: 2rem;
	/* margin-bottom: 0.625em; */
