	text-justify: auto;
}

#left{
	width: 300px;
	height: 300px;
	float: left;
	border: 1px solid #000;
}
	
#right{
	width: 300px;
	height: 300px;
	float: right;
	border: 1px solid #000;
}
	
#middle{
	width: 300px;
